import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import ENSName from 'components/ens/ENSName.js';


const TokenSaleCard = ({cart, owner, addToCart, inCart, tokenId, thumbnail, askPrice}) => { 
    let [error, setError] = useState(undefined);

    let className = "token-sale-card";
    if (inCart) {
        className += " in-cart";
    }

    useEffect(() => {
        setError(undefined);
    }, [cart, setError]);
    useEffect(() => {
        if (error) {
            setTimeout(() => {
                setError(undefined);
            }, 8000);
        }
    }, [error, setError]);
    if (error) {
        className += " has-error";
    }
    return (
        <div onClick={() => {
            if (askPrice) {
                addToCart();
            } else {
                setError("Already sold");
            }
        }} className={className}>
          {error &&
           <div className="already-sold">
             This player has been already sold. You can make an offer on secondary marketplaces though!
           </div>}
          <img src={thumbnail} className="token-preview"/>
          <div className="card-info">
            <div className="token-name">
              SP-{tokenId}
            </div>
            {askPrice ? <div className="ask-price">{askPrice} ETH</div> :
             <div className="ask-price not-for-sale">Owned by <ENSName address={owner}/></div>}
            {inCart && <div className="check"/>}
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TokenSaleCard);
