import * as THREE from 'three'
import {isMobile} from 'react-device-detect';
import useSpline from '@splinetool/r3f-spline'
import { OrthographicCamera } from '@react-three/drei'
import React, { useRef } from 'react'
import Mushroom from './Mushroom.js';
import Mushroom2 from './Mushroom2.js';
import Mushroom3 from './Mushroom3.js';

export default function Scene({ ...props }) {
  const { nodes, materials } = useSpline('https://prod.spline.design/4CF6xEKxsHbnQn4O/scene.splinecode')
  const sceneCamera = useRef()

  const textureLoader = new THREE.TextureLoader();
  const threeTone = textureLoader.load( 'threeTone.jpg' );
  threeTone.minFilter = THREE.NearestFilter;
  threeTone.magFilter = THREE.NearestFilter;

  const mushroomMat = new THREE.MeshToonMaterial( { color: 0x049EF4, gradientMap: threeTone });

  window.addEventListener("message", receiveMessage, false);
  function receiveMessage(event) {
    if (event.origin !== "https://zequencer.mypinata.cloud") {
      return;
    } else {
      var message = event.data;

      if (Array.isArray(message)) {
        if (message.length == 4) {
          mushroomMat.color.r = message[3]
          mushroomMat.color.g = message[1]
          mushroomMat.color.b = message[0]
          mushroomMat.color.addScalar(0.25 * message[2])
        }
      }
    }
  }

  return (
    <>
      <group {...props} dispose={null}>
        <Mushroom position={[145, -100, 0]} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />
        <Mushroom3 position={[-110, -50, 0]} scale={2} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />
        <Mushroom3 position={[110, -90, 0]} scale={1.5} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />

        <Mushroom2 position={[100, 0, 0]} scale={1} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />
        <Mushroom3 position={[20, -25, -190]} scale={1} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />

        <Mushroom position={[45, 10, -190]} scale={0.1} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />
        <Mushroom2 position={[-20, 25, -290]} scale={0.7} mushroomMat={mushroomMat} nodes={nodes} materials={materials} />
        <directionalLight
          name="Directional Light"
          castShadow
          intensity={1}
          shadow-mapSize-width={1024}
          shadow-mapSize-height={1024}
          shadow-camera-near={1}
          shadow-camera-far={2500}
          shadow-camera-left={-1250}
          shadow-camera-right={1250}
          shadow-camera-top={1250}
          shadow-camera-bottom={-1250}
          position={[-461.22, 260.59, 1269.06]}
        />
       <OrthographicCamera
          name="Personal Camera"
          ref={sceneCamera}
          makeDefault={true}
          far={100000}
          near={-100000}
          position={[0, 0, 1000]}
          zoom={isMobile ? .15 : .27}
        />
        <hemisphereLight name="Default Ambient Light" intensity={1} color="#eaeaea" position={[0, 1, 0]} />
      </group>
    </>
  )
}
