import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {isMobile} from 'react-device-detect';

const Cart = ({onClickBuy, tokensOnSale, cart, removeFromCart, clearCart}) => { 

    const goToLink = (tokenId) => {
        window.open('/sp/' + tokenId, '_newtab');
    };
    if (cart.length === 0) {
        return '';
    }

    return (
        <div className="cart">
          <div className="cart-options">
            {cart.map(
                tokenId =>
                <div
                  key={tokenId}
                  onClick={() => goToLink(tokenId)}
                  className="cart-option">
                  <img src={tokensOnSale.find(x => x.tokenId === tokenId).thumbnail}/>
                  <div className="song-info">
                    <div className="song-title">
                      Psilocybin EP <a href={"/sp/" + tokenId} target="_blank" rel="noreferrer"></a>
                    </div>
                    <div className="artist">
                      Keyon Christ
                    </div>
                  </div>
                </div>)}
          </div>
          <div className="buy-info-container">
            <div
              onClick={onClickBuy}
              className="pushable buy-button">
              {cart.length === 1 ? "Buy SP-" + cart[0] : `Buy all ${cart.length} players`}
            </div>
            <div className="price-info">
              for {isMobile ? "" : "a total of "} <span className="total-price">{round(cart.length * 0.4)} ETH</span>
            </div>
            <div onClick={clearCart} className="clear-cart">
              Clear Cart
            </div>
          </div>
        </div>);
};

const round = (x) => Math.round(x*10)/10;

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Cart);
