import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './recording.scss';

const RecordingIndicator = (props) => { 
    let [playing, setPlaying] = useState(false);

    useEffect(() => {
        window.addEventListener("message", onMessage, false);
    }, [setPlaying]);

    const onMessage = useCallback((e) => {
        if (e.data && Array.isArray(e.data.type) && e.data.type[0] === "sequencer.start") {
            setPlaying(true);
        } else if (e.data && Array.isArray(e.data.type) && e.data.type[0] === "sequencer.stop") {
            setPlaying(false);
        }
    }, [setPlaying]);


    let className = " recording-indicator";
    if (playing) {
        className += " is-recording";
    }

    return (<div className={className}>
            </div>
           );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RecordingIndicator);
