import React, { useRef, useEffect, useCallback, useState} from 'react';
import {useConsumeWheel} from 'components/hooks/useConsumeWheel.js';
import {connect} from 'react-redux';
import './menu.scss';

const PlayerChooserMenu = ({tokenId}) => { 
    let ref = useRef();
    let [open, setOpen] = useState(false);
    let players = new Array(20).fill(0);
    for (let i=1; i <= 20; i++) {
        players[i-1] = i;
    }
    useConsumeWheel(ref);
    return (
        <div
          ref={ref}
          onClick={(e) => {
            e.stopPropagation();
            setOpen(!open);
        }} className="player-menu">
          SP-{tokenId}
          <div onClick={() => setOpen(!open)} className={open ? "arrow-up" : "arrow-down"}/>
          
          {open &&
           <div className="player-menu-items">
           {players.map(
               _tokenId => <div
                             onClick={() => window.location.href = "/sp/" + _tokenId}
                             className={tokenId === _tokenId ? "selected go-to-player" : "go-to-player"}>
                             <div>SP-{_tokenId}</div> <img className="menu-thumb" src={"/sp-thumbnail-" + _tokenId + ".png"}/>
                           </div>)}
           </div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlayerChooserMenu);
