import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './credits.scss';

const Credits = (props) => { 
    let [show, setShow] = useState(false);
    return (
        <div className="song-credits">
          <div onClick={() => setShow(!show)} className={show ? "credits-btn show" : "credits-btn"}>
            Credits
          </div>
          {show && <div>
                     <CreditPart type="Vocals, Producer" names={["Keyon Christ"]}/>
                     <CreditPart type="Writing" names={["Keyon Christ", "Zamorrah"]}/>
                     <CreditPart type="Co-Producer" names={["CLLLAPS", "JonnyWood", "Oshi"]}/>
                     <CreditPart type="Mixing" names={["JonnyWood", "Wil Anspach"]}/>
                     <CreditPart type="Recording" names={["Wil Anspach"]}/>
                     <CreditPart type="Location" names={["Sunset and Gower Recording Studios"]}/>
                     <CreditPart type="Powered By" names={["Zequencer", "Indigo Sound"]}/>
                   </div>}
        </div>
    );
}

const CreditPart = ({type, names}) => {
    return (
        <div className="credit-part">
          <div className="credit-type">
            {type}
          </div>
          <div className="credit-names">
            {names.map((x, i) => <div className="credit-name">{x + (i < names.length - 1 ? ', ' : '')}</div>)}
          </div>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Credits);
