import React, { useRef, useEffect, useCallback, useState} from 'react';
import Credits from "components/credits/Credits.js";
import SporePlaylist from 'components/playlist/SporePlaylist.js';
import {connect} from 'react-redux';
import {isMobile} from 'react-device-detect';
import './sidebar.scss';
import SporeOption from './SporeOption.js';

const Sidebar = ({playerChosen, setPlayerChosen, showStemPlayer, setShowStemPlayer}) => { 
    let ref = useRef();
    let [selectedPlayer, setSelectedPlayer] = useState(undefined);
    let [mouse, setMouse] = useState();
    let [over, setOver] = useState();

    const onMouseMove = useCallback((e) => {
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        let y = e.clientY - rect.top;
        setMouse({x,y});
    }, [setMouse]);

    const onMouseLeave = useCallback(() => {
        setMouse(undefined);
    }, [setMouse]);

    let brect = ref.current && ref.current.getBoundingClientRect();

    let className = "caurosel";
    if (playerChosen) {
        className += " player-chosen";
    }
  
    let tokens = new Array(20).fill(0);
    for (let i=1; i < 20; i++) {
        tokens[i-1] = i;
    }
    return (
        <div onMouseLeave={onMouseLeave} ref={ref} onMouseMove={onMouseMove} className={className}>
          {mouse && <div className="mouse-radial" style={{left: mouse.x-500, top: mouse.y-500}}></div>}
          <SporeOption
                playerChosen={playerChosen}
                brect={brect}
                setShowStemPlayer={setShowStemPlayer}
                showStemPlayer={showStemPlayer}
                mouse={mouse}
                isOver={over===playerChosen}
                setOver={setOver}
                setPlayerChosen={setPlayerChosen} rarity="" tokenId={playerChosen}
                thumbnail={isMobile ? "/sp-" + playerChosen + ".png" : '/sp-' + playerChosen + '.png'}
              />
       </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Sidebar);
