import React, { useEffect, useCallback, useState} from 'react';
import Spore from './Spore.js';
import {connect} from 'react-redux';

const SporeWrapper = (props) => { 
    let tokenId = props.match && props.match.params.tokenId;
    if (tokenId) {
        tokenId = parseInt(tokenId);
    }
    let [playerChosen, setPlayerChosen] = useState(tokenId);
    let [showStemPlayer, setShowStemPlayer] = useState(tokenId !== undefined);
    console.log("tokenId=", tokenId, playerChosen, showStemPlayer);
 
    return (<Spore
              playerChosen={playerChosen}
              setPlayerChosen={setPlayerChosen}
              setShowStemPlayer={setShowStemPlayer}
              showStemPlayer={showStemPlayer}/>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeWrapper);
