import React, { useEffect, useCallback, useState} from 'react';
import BuyButton from 'components/buy/BuyButton.js';
import {truncate} from 'components/spore/AudioPlayer.js';
import {connect} from 'react-redux';
import { useSporeOwner} from 'components/hooks/useSporeOwner.js';
import './owner.scss';

const SporeOwner = ({tokenId, setBuying, completedBuy}) => { 
    let {ensName, owner} = useSporeOwner({tokenId});
    if (completedBuy) {
        ensName = "You";
    }
    if (!ensName && !owner) {
        return <BuyButton setBuying={setBuying} tokenId={tokenId}/>;
    }
    return (
        <div className="spore-owner">
          Owned by <span className="owner-name">{ensName || truncate(owner)}</span>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeOwner);
