
import React from 'react';
import {useRef, useState, useEffect} from 'react';
import styles from "./Overlay.module.css";

export function Overlay({ playerChosen, scroll, setShowStemPlayer, setPlayerChosen }) {
    const scrollContainer = useRef();
    useEffect(() => {
        if (!playerChosen && scroll.current !== 0) {
            if (scrollContainer.current) {
                scrollContainer.current.scrollTo(0, scrollContainer.current.scrollHeight* scroll.current);
            }
        }
    }, [playerChosen]);

  return (
    <div
      ref={scrollContainer}
      className={styles.scrollContainer}
      onScroll={(e) => {
        const scroll01 =
          e.target.scrollTop / (e.target.scrollHeight - window.innerHeight);
        scroll.current = scroll01;
      }}
    >
      <section
        style={{ height: "50vh" }}
        className={styles.sectionLeft + " " + styles.sectionHero}
      >
        <header>
          <h1>
            LORE
          </h1>
          <svg
            width="15"
            height="30"
            viewBox="0 0 30 71"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <rect
              x="1.5"
              y="1.5"
              width="27"
              height="45"
              rx="13.5"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
            />
            <path
              d="M15 12V20"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
              stroke-linecap="round"
            />
            <path
              d="M8 54L14.5 60.5L21 54"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
            <path
              d="M8 63L14.5 69.5L21 63"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
          </svg>
        </header>
      </section>
      <section style={{ height: "180vh" }} className={styles.sectionRight + " " + styles.sectionFirst}>
        <p>
          Do you see it?
          <br />
          That light in the dark?
          <br />
          It calls to you.
        </p>
      </section>
      <section style={{ height: "50vh" }} className={styles.sectionLeft + " " + styles.sectionSecond}>
       <p>
          Deep underground...
        </p>
      </section>
      <section style={{ height: "150vh" }} className={styles.sectionRight + " " + styles.sectionLast}>
        <p>
          The spores resonate.
          <br />
          Awakening from their slumber.
        </p>
        <button
              onClick={() => {
                window.location.href='https://spores.vision';
              }}
              className={"pushable " + styles.spButton}> Home</button>
      </section>
    </div>
  );
}
