import React, { useEffect, useRef, useCallback, useState} from 'react';
import AudioPlayer from './AudioPlayer.js';
import BackendQuery from "decentralized/BackendQuery.js";
import {connect} from 'react-redux';
import {getLatestSpore} from './getSpores.js';
import UnknownProgressBar from "components/progressbar/UnknownProgressBar.js";

const links = {
    1: "https://opensea.io/assets/0x7269c1a1b8aba1390770193771f92bd722c6a77b/1",
    2: "https://opensea.io/assets/0x7269c1a1b8aba1390770193771f92bd722c6a77b/2",
    3: "https://opensea.io/assets/0x7269c1a1b8aba1390770193771f92bd722c6a77b/3",
    4: "https://opensea.io/assets/0x7269c1a1b8aba1390770193771f92bd722c6a77b/4",
};
const SimplePlayer = ({store, audioUnit, selected, tokenId, spore, setShowStemPlayer, setPreset, preset}) => { 
    let [playing, setPlaying] = useState(false);
    let [latestSpore, setLatestSpore] = useState(undefined);
    let [sporeMetadata, setSporeMetadata] = useState();
    let left = useRef(false);

    useEffect(() => {
        if (audioUnit.current) {
            audioUnit.current.dispose();
            audioUnit.current = undefined;
        }
        setLatestSpore(undefined);
        getLatestSpore(selected, tokenId, setSporeMetadata).then((spore) => {
            if (left.current) {
                spore.audioUnit.dispose();
                return;
            }
            if (spore) {
                audioUnit.current = spore.audioUnit;
            }
            setLatestSpore(spore);
        });

        return () => {
        };
    }, [setLatestSpore, tokenId, selected, setSporeMetadata]);

    useEffect(() => {
        return () =>{
            left.current = true;
            if (audioUnit.current) {
                audioUnit.current.dispose();
            }
        }
    }, []);


    const goToLink = useCallback(() => {
        window.open(links[tokenId], '_blank');
    }, [tokenId]);

    let className = "simple-player";
    if (!latestSpore) {
        className += " hide";
    }
    return (
        <div className={className}>
          {latestSpore === null ? <div
                                    className="no-spores">No spores have been made yet.</div> :
           <div>
             {!latestSpore && <UnknownProgressBar intervalTime={100} intervalPiece={3} text={selected ? "Loading Spore #" + selected.sporeId : "Loading Latest Spore"}/>}
             {sporeMetadata &&
              <AudioPlayer
                store={store}
                spore={latestSpore}
                setPreset={setPreset}
                sporeMetadata={sporeMetadata}
                preset={preset}
                saturation={80}
                heat={latestSpore && latestSpore.heat}
                creator={sporeMetadata.creator}
                audioUnit={latestSpore && latestSpore.audioUnit}/>}
           </div>}
        {!selected && <div
            onClick={() => setShowStemPlayer(true)}
            className="pushable stem-player-button">
            Expand Player
                      </div>}
          {!selected &&
           <div
             onClick={() => goToLink()}
             className="pushable stem-player-button view-on-opensea">
           {links[tokenId] ? <div>
               OpenSea <span></span>
                             </div> : "Soon"}
           </div>}
          {selected &&
           <div className="stem-player-button mint-spore">
             Mint Spore
             <div className="spore-tooltip">
               to mint, you must own a spore player
             </div>
           </div>}
       </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SimplePlayer);
