import { ethers } from "ethers";
import React, { useEffect, useCallback, useState} from 'react';

export const useTokensOnSale = () => {
    const [tokensOnSale, setTokensOnSale] = useState([]);

    useEffect(() => {
        // should query all of these along w buy-now price/whether theres an ask
        getTokensOnSale().then(setTokensOnSale);
        // for now just assume theyre for sale and for 0.4 eth
    }, [setTokensOnSale]);

    return {tokensOnSale, setTokensOnSale};
};

const getTokensOnSale = () => {
    return new Promise((resolve) => {
        fetch("https://zequencer.io/nft-info")
            .then(r => r.json())
            .then(r => resolve(r));
    });
};
