const StemPlayer = ({
    tokenId,
    setShowPlaylistMenu,
    showPlaylistMenu,
    setShowStemPlayer,
    showCredits,
    setShowCredits
}) => { 
    let ref = useRef();
    let [buying, setBuying] = useState(false);
    let [completedBuy, setCompletedBuy] = useState(false);

    const touchHandler = (event)=> {
        if(event.touches.length > 1){
            //the event is multi-touch
            //you can then prevent the behavior
            event.preventDefault()
        }
    };
    
    useEffect(() => {
        window.addEventListener("touchstart", touchHandler, false);
        return () => {
            window.removeEventListener("touchstart", touchHandler, false);
        };
    }, []);

   useEffect(() => {
        var root = document.getElementsByTagName( 'html' )[0]; // '0' to assign the first (and only `HTML` tag)
        root.setAttribute( 'class', 'bad-scroll' );
        return () => {
            root.setAttribute( 'class', '' );
        };
    }, []);

    let url = tokenId <= 20 ? "https://zequencer.mypinata.cloud/ipfs/QmZNrYg1iqPU6nzyUBSoF598YsJRsdXbu2iUCvMhjJnsrJ//?tokenId=" + tokenId + "&gallery=true" : players[tokenId];
    /*
    if (tokenId === 6) {
        url = "https://zequencer.mypinata.cloud/ipfs/QmaauzFwE2wZZU2mqeofpG4PnFEaZZ9wAnKm95oT7zBG53/?tokenId=6&gallery=true";
    }
    */
    //let url = tokenId <= 20 ? "https://zequencer.mypinata.cloud/ipfs/Qmb57sHJC8Zq6AYRPy6gGzqYQpevjRAnPbkqRbJTPt1n4o/?tokenId=" + tokenId + "&gallery=true" : players[tokenId];
    //let url = tokenId <= 20 ? "http://localhost:9090/nftviewer/QmTu96UweijKQquvTubt25jAM2SyGDm57T7pbEJT7ADZ33/?tokenId=" + tokenId + "&gallery=true" : players[tokenId];

    window.stemIFrame = ref;
    return [
        (buying && <BuyPlayerFlow setCompletedBuy={setCompletedBuy} setBuying={setBuying} tokenId={tokenId}/>),
        (isMobile && <img className={showPlaylistMenu ? "spores-playlist-button opened" : "spores-playlist-button"} onClick={() => setShowPlaylistMenu(!showPlaylistMenu)} src="/spores-icon-glow2.svg"/>),
        <RecordingIndicator/>,
        <SporeOwner completedBuy={completedBuy} setBuying={setBuying} tokenId={tokenId}/>,
        <SporePlaylist tokenId={tokenId} showCredits={showCredits} setShowCredits={setShowCredits} setShowStemPlayer={setShowStemPlayer}/>,
        (<iframe
           ref={ref}
           sandbox="allow-scripts allow-same-origin"
           //height={600}
           //width={600}
           height={tokenId <= 20 ? "400" : "360"}
           width={tokenId <= 20 ? "340" : "310"}
           aria-describedby="spore"
           alt="spore.vision"
           src={url}
           className="spore-player-embed zora-mediaObject css-hnzuw7 zora-mediaObject--mediaLoaded zora-mediaObject--isFullPage"
           style={{borderWidth: "0px"}}>
         </iframe>)
    ];
}


const mapStateToProps = (state, props) => {
  return {
      showPlaylistMenu: state.playlist.showPlaylistMenu
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setShowPlaylistMenu: x => dispatch(setShowPlaylistMenu(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StemPlayer);
