import React from 'react';
import {useTokenParam} from 'components/hooks/useTokenParam.js';
import Learn from 'components/learn/Learn.js';
import BuyNow from 'components/buy/BuyNow.js';
import Spore from 'components/spore/Spore.js';
import {useState, useEffect} from 'react';
import { Suspense, useRef } from 'react'
import { Canvas } from '@react-three/fiber'
import Scene from './Scene'
import Landing from './Landing';
import { Overlay } from './Overlay';
import { EffectComposer, SSAO, Bloom } from "@react-three/postprocessing";
import { KernelSize } from 'postprocessing'
import './styles.css';

export default function App(props) {
    useEffect(() => {
        document.title = "Spores";
    }, []);

    let tokenId = useTokenParam(props);
    let [playerChosen, setPlayerChosen] = useState(tokenId);
    let [showStemPlayer, setShowStemPlayer] = useState(tokenId !== undefined);
    let [buyingMode, setBuyingMode] = useState(props.buying);
    let [learningMode, setLearningMode] = useState(props.learning);

    useEffect(() => {
        if (props.buying || props.learning) {
            return;
        }
        if (learningMode) {
            window.history.pushState('page2', 'Title', '/learn');
            return;
        }
        if (playerChosen !== undefined) {
            window.history.pushState('page2', 'Title', '/sp/' + playerChosen);
        } else {
            window.history.pushState('page2', 'Title', '/');
        }
    }, [playerChosen, learningMode]);
 
    const scroll = useRef(0);

    if (learningMode) {
        return (
            <Learn goBack={() => {
                setLearningMode(false);
            }}/>
        );
    }
    if (showStemPlayer || playerChosen) {
        return (<Spore
                  store={props.store}
                  playerChosen={playerChosen}
                  setPlayerChosen={setPlayerChosen}
                  setShowStemPlayer={setShowStemPlayer}
                  showStemPlayer={showStemPlayer}/>);
    }
    if (buyingMode) {
        return (
            <BuyNow goBack={() => setBuyingMode(false)}></BuyNow>
        );
    }
    return [
      <Landing
        setBuyingMode={setBuyingMode}
        playerChosen={playerChosen}
        setPlayerChosen={setPlayerChosen}
        setShowStemPlayer={setShowStemPlayer}/>
    ];
}
