const CHAOS_COLLECTION = "0x8427e46826a520b1264b55f31fcb5ddfdc31e349";
const CHAOS_METADATA  = "https://opensea.mypinata.cloud/ipfs/bafybeic3pw5dd6tsu67mpgcm3mj5x335nfmq7xpesomv3uyuqluv2srsg4/";
const OPEN_SEA_PINATA = "https://opensea.mypinata.cloud/ipfs/";

const getMetadata = (url) => {
    return new Promise(resolve => {
        if (url.startsWith("ipfs://")) {
            url = "https://zora-prod.mypinata.cloud/ipfs/" + url.slice(7);
        }
        fetch(url).then(
            r => r.json())
            .then(
                r => {
                    let {animation_url, audio_url} = r;
                    console.log("ANIMATION_URL=%s audio_url=%s", animation_url, audio_url);
                    let json = {
                        ... r
                    };
                    if (animation_url) {
                        if (animation_url.includes("ipfs")) {
                            json.ipfs = animation_url.slice(7);
                            json.url =OPEN_SEA_PINATA + animation_url.slice(7);
                        } else {
                            json.url = animation_url;
                        }
                    };
                    if (audio_url) {
                        if (audio_url.includes("/ipfs/")) {
                            let indexOf = audio_url.indexOf("/ipfs");
                            json.ipfs = audio_url.slice(indexOf+6);
                        }
                    }
                    resolve(json);
                });
    });
};

export const jack = async (url, getTokenMetadata) => {
    if (url.includes("0x8427e46826a520b1264b55f31fcb5ddfdc31e349")) {
        // we are in a chaos drop
        let parts = parse_url(url);
        let path = parts.path;
        let splitted = path.split("/").filter(x => x !== "");
        let tokenId = splitted[3];
        let _url = CHAOS_METADATA + tokenId + ".json";
        return await getMetadata(_url);
    }
    if (url.includes("opensea") || url.includes("zora") || url.includes("sound.xyz")) {
        // then we need to figure out the 
        let ret = await getZoraAPI(url);
        if (!ret) {
            let {contract, tokenId} = await getParts(url)
            console.log("parts from contract was=", contract, tokenId, url);
            let meta = await getTokenMetadata(contract, tokenId);
            console.log("META returned for token=", meta);
            return await getMetadata(meta);
        } else {
            let audio_url = ret.url;
            let indexOf = audio_url.indexOf("/ipfs");
            let json = {
                ... ret.metadata,
                url: audio_url,
            };
            return json;
        }
    }
};

const getParts = async (url) => {
    let part = "/ethereum/";
    if (url.includes("sound.xyz")) {
        //let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/soundxyz/" : "https://zequencer.io/soundxyz/";
        let baseURL = "https://zequencer.io/soundxyz/";
        let indexOf = url.indexOf("sound.xyz");
        let soundxyz = url.slice(indexOf + 10);
        let ret = await fetch(baseURL + soundxyz);
        let json = await ret.json();
        url = json.contractAddress + "/" + 1;
        return {contract: json.contractAddress, tokenId: "1"};
    } else if (url.includes("zora.co")) {
        part = "/collections/";
    }
    let indexOf = url.indexOf(part);
        url = url.slice(indexOf + part.length);
        let p = url.split("/");
    if (p[0] === "zora") {
        p[0] = "0xabEFBc9fD2F806065b4f3C237d4b59D9A97Bcac7";
    }
    return {
        contract: p[0],
        tokenId: parseInt(p[1]),
    };
};

const getZoraAPI = async (url) => {
    if (url.includes("zora")) {
        let part = "/collections/";
        let indexOf = url.indexOf(part);
        url = url.slice(indexOf + part.length);
        let parts = url.split("/")
        if (parts[0] === "zora") {
            parts[0] = "0xabEFBc9fD2F806065b4f3C237d4b59D9A97Bcac7";
        }
        url = parts.join("/");
    } else if (url.includes("sound.xyz")) {
        //let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/soundxyz/" : "https://zequencer.io/soundxyz/";
        let baseURL = "https://zequencer.io/soundxyz/";
        let indexOf = url.indexOf("sound.xyz");
        let soundxyz = url.slice(indexOf + 10);
        let ret = await fetch(baseURL + soundxyz);
        let json = await ret.json();
        url = json.contractAddress + "/" + 0;
    } else {
        let part = "/ethereum/";
        let indexOf = url.indexOf(part);
        url = url.slice(indexOf + part.length);
    }
    
    //let baseURL = window.location.hostname === "localhost" ? "http://localhost:9090/jacker/" : "https://zequencer.io/jacker/";
    let baseURL = "https://zequencer.io/jacker/";
    try {
        let ret = await fetch(baseURL + url);

        return await ret.json();
    } catch(e) {
        return undefined;
    }
};
 
function parse_url(url) {
    var pattern = RegExp("^(([^:/?#]+):)?(//([^/?#]*))?([^?#]*)(\\?([^#]*))?(#(.*))?");
    var matches =  url.match(pattern);
    return {
        scheme: matches[2],
        authority: matches[4],
        path: matches[5],
        query: matches[7],
        fragment: matches[9]
    };
}

const isChaos = (url) => {
    return url.includes(CHAOS_COLLECTION);
};
