import React, { useEffect, useCallback, useState} from 'react';
import FinalizePurchase from './FinalizePurchase.js';
import Cart from './Cart.js';
import TokenSaleCard from './TokenSaleCard.js';
import {connect} from 'react-redux';
import {useBuy} from '../hooks/useBuy.js';
import {useTokensOnSale} from '../hooks/useTokensOnSale.js';
import { ConnectButton, darkTheme } from '@rainbow-me/rainbowkit';
import './buy-now.scss';
import { useAccount } from 'wagmi';

const BuyNow = ({goBack}) => { 
    const buy = useBuy();
    const {data, isError, isLoading } = useAccount();
    console.log("acc data =", data);
    let [cart, setCart] = useState([]);
    let [buying, setBuying] = useState();
    let {setTokensOnSale, tokensOnSale} = useTokensOnSale();

    const  onCompletedBuy = useCallback((tokenId) => {
        if (!data) {
            return;
        }
        console.log("onCompletedBuy account=%s tokenId=%s", data.address, tokenId);
        tokensOnSale[tokenId - 1] = {
            ... tokensOnSale[tokenId - 1],
            owner: data.address,
            askPrice: undefined
        };
        console.log("setting tokenId=%s", tokenId, tokensOnSale[tokenId - 1]);
        setTokensOnSale([... tokensOnSale]);
    }, [tokensOnSale, setTokensOnSale, data]);

    const addToCart = useCallback((tokenId) => {
        if (!cart.includes(tokenId)) {
            if (cart.length === 0) {
                setCart([... cart, tokenId]);
            } else {
                setCart([tokenId]);
            }
        } else {
            setCart(cart.filter(x => x !== tokenId));
        }
    }, [cart, setCart]);

    const onClickBuy = useCallback(() => {
        setBuying(cart);
    }, [cart, setBuying]);

    return (
        <div className="buy-now-view">
          {buying && <FinalizePurchase
                       account={data && data.address}
                       onCompletedBuy={onCompletedBuy}
                       onDone={() => {
                           setBuying(false);
                           setCart([]);
                       }}
                       tokensOnSale={tokensOnSale} onCancel={() => setBuying(false)} cart={cart} setCart={setCart}/>}
          <div className={cart.length > 0 ? "buy-grid-header with-cart" : "buy-grid-header"}>
            <div className="logo-container">
            <img
              onClick={() => goBack()}
              className="pushable spores-logo" src="/spores-logo-black.svg"/>
            </div>
            {cart.length === 0 && <div className="buy-page-title">Spore Players</div>}
            <Cart
              onClickBuy={onClickBuy}
              clearCart={() => setCart([])} cart={cart} removeFromCart={addToCart} tokensOnSale={tokensOnSale}/>
            <div className="connect-button-wrapper">
              <ConnectButton
                accountStatus="address" 
                showBalance={{ smallScreen: false, largeScreen: false }}
                label="Connect Wallet"
              />
            </div>
          </div>
          <div className="buy-grid">
            {tokensOnSale.map(
                ({tokenId, owner, askPrice, thumbnail}) =>
                <TokenSaleCard
                  owner={owner}
                  inCart={cart.includes(tokenId)}
                  addToCart={() => addToCart(tokenId)}
                  cart={cart}
                  tokenId={tokenId}
                  thumbnail={thumbnail}
                  askPrice={askPrice}/>)}
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(BuyNow);
