import React, { useEffect, useCallback, useState} from 'react';
import {useTokensOnSale} from 'components/hooks/useTokensOnSale.js';
import { useAccount } from 'wagmi';
import { useEnsName } from 'wagmi';

export const useSporeOwner = ({tokenId}) => { 
    let {tokensOnSale} = useTokensOnSale();
    let token = tokensOnSale && tokensOnSale.find(x => x.tokenId === tokenId);
    console.log('token = ', tokenId, token, tokensOnSale);
    let address = token && token.owner;
    const { data, isError, isLoading } = useEnsName({
        address
    });

    return {
        ensName: data,
        owner: address
    };
};
 
