import React, { useEffect, useCallback, useState} from 'react';
import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import {connect} from 'react-redux';
import { AsksV11__factory } from "@zoralabs/v3/dist/typechain/factories/AsksV11__factory";
import { ethers } from "ethers";
import mainnetZoraAddresses from "@zoralabs/v3/dist/addresses/4.json"; // Mainnet addresses, 4.json would be Rinkeby Testnet 
import { IERC721__factory } from "@zoralabs/v3/dist/typechain/factories/IERC721__factory";
import { IERC20__factory } from "@zoralabs/v3/dist/typechain/factories/IERC20__factory";
import { ZoraModuleManager__factory } from "@zoralabs/v3/dist/typechain/factories/ZoraModuleManager__factory";
import './zora.scss';

const Zora = (props) => { 
    let [contractAddress, setContractAddress] = useState(window.localStorage.getItem("zora-contract-test") || "");
    let [tokenId, setTokenId] = useState("");
    let [askPrice, setAskPrice] = useState("");
    let [approved, setApproved] = useState(0);
    let [running, setRunning] = useState(false);
    let [completed, setCompleted] = useState(false);

    const onClick = useCallback(async () => {
        setRunning(true);
        window.localStorage.setItem("zora-contract-test", contractAddress);
         const provider = new ethers.providers.Web3Provider(window.ethereum, "any");
        // Prompt user for account connections
        let accounts = await provider.send("eth_requestAccounts", []);
        const signer = provider.getSigner();

        const moduleManagerAddress = mainnetZoraAddresses.ZoraModuleManager;
        const ownerAddress = "0xdc94060e37dcb8816188508536595019c8F0C98a";// Owner of the assets (my test acct)

        const erc721Contract = IERC721__factory.connect(contractAddress, signer);

        // Initialize ERC20 currency demo contract
        // Initialize Zora V3 Module Manager contract 
        const moduleManagerContract = ZoraModuleManager__factory.connect(mainnetZoraAddresses.ZoraModuleManager, signer);
        const erc721TransferHelperAddress = mainnetZoraAddresses.ERC721TransferHelper;
        let approved = await erc721Contract.isApprovedForAll(
            ownerAddress, // NFT owner address
            erc721TransferHelperAddress // V3 Module Transfer Helper to approve
        );
        console.log("token transfer module approved?", approved);
        setApproved(1);
        
        // If the approval is not already granted, add it.
        if (approved === false) {
            // Notice: Since this interaction submits a transaction to the blockchain it requires an ethers signer.
            // A signer interfaces with a wallet. You can use walletconnect or injected web3.
            console.log("approving token transfer module");
            await erc721Contract.setApprovalForAll(erc721TransferHelperAddress, true);
        }
        setApproved(2);

        // Approving Asks v1.1
        approved = await moduleManagerContract.isModuleApproved(ownerAddress, mainnetZoraAddresses.AsksV1_1);
        console.log("ASKS ARE APPROVED? ", approved);

        if (approved === false) {
            console.log("approving asks module");
            await moduleManagerContract.setApprovalForModule(mainnetZoraAddresses.AsksV1_1, true);
        }
        setApproved(3);

        // create ask
        const askModuleContract = AsksV11__factory.connect(mainnetZoraAddresses.AsksV1_1, signer);
        let ask = await askModuleContract.askForNFT(contractAddress, 1);
        if (ask.askPrice.toNumber() === 0) {
            // create ask
            const _askPrice = ethers.utils.parseEther(askPrice) // 100 ETH Sale Price
//            const ownerAddress = "0xF296178d553C8Ec21A2fBD2c5dDa8CA9ac905A00"; // Owner of the assets
            const findersFeeBps = "0"; // 2% Finders Fee (in BPS)
            
            // Calling Create Ask
            // Notice: Since this interaction submits a transaction to the blockchain it requires a signer.
            // A signer interfaces with a wallet. You can use walletconnect or injected web3.
            console.log("creating ask");
            let tx = await askModuleContract.createAsk(
                contractAddress,
                tokenId,
                _askPrice,
                "0x0000000000000000000000000000000000000000", // 0 address for ETH sale
                ownerAddress,
                findersFeeBps
            );
            tx.wait(2).then(x => setRunning(false));
        }
    }, [setApproved, setRunning, askPrice, tokenId, contractAddress]);

    return (
        <div className="zora-helper">
          <div className="zora-inner">
            <div className="z-label">
              Contract Address
            </div>
            <input
              value={contractAddress}
              onChange={(e) => setContractAddress(e.target.value)}
            />
            <div className="z-label">
              Token ID
            </div>
            <input
              value={tokenId}
              onChange={(e) => setTokenId(e.target.value)}
            />
            <div className="z-label">
              Ask Price (ETH)
            </div>
            <input
              value={askPrice}
              onChange={(e) => setAskPrice(e.target.value)}
            />
            <div
              onClick={onClick}
              className="pushable big-button">
              Approve and List Ask
            </div>
            {running && <UnknownProgressBar width={150} text="Running"/>}
            <div className="approvals-completed">
              Approvals Completed: {approved} / 3
            </div>
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Zora);
