import React, { useRef, useEffect, useCallback, useState} from 'react';
import PlayerChooserMenu from './PlayerChooserMenu.js';
import Credits from "components/credits/Credits.js";
import SongTitle from './SongTitle.js';
import SporePlaylist from 'components/playlist/SporePlaylist.js';
import {connect} from 'react-redux';
import {isMobile} from 'react-device-detect';

const SporeOption = ({
    showStemPlayer,
    setShowStemPlayer,
    brect,
    tokenId, playerChosen, isOver, mouse, setOver, rarity, thumbnail, setPlayerChosen
}) => {
    let ref= useRef();
    let [currentSong, setCurrentSong] = useState("Twisting Faith");

    let rect = ref.current && ref.current.getBoundingClientRect();
    let _x = rect && mouse && mouse.x - rect.left + brect.left;
    let _y= rect && mouse && mouse.y - rect.top + brect.top;
    let className = "spore-player-option";
    if (playerChosen === tokenId) {
        className += " chosen";
    } else if (playerChosen) {
        className += " unchosen";
    }

    const onClick = useCallback(() => {
        if (showStemPlayer) {
            return;
        }
        if (playerChosen === tokenId) {
            setShowStemPlayer(undefined);
            setPlayerChosen(undefined);
        } else {
            setPlayerChosen(tokenId);
            setShowStemPlayer(true);
        }
    }, [setPlayerChosen, showStemPlayer, playerChosen, tokenId]);
    
    console.log("spore option=", setShowStemPlayer);
    return (
        <div
          ref={ref}
          onMouseOver={() => setOver(tokenId)}
          className={className}>
          {isOver && mouse && <div className="mouse-radial" style={{left: _x-500, top: _y-500}}></div>}
          <div
            onClick={onClick}>
            <img src={thumbnail}/>
            <div
              className="sp-fields">
              <PlayerChooserMenu tokenId={tokenId}/> 
              <div className="sp-rarity">
                {rarity}
              </div>
            </div>
          </div>
          {showStemPlayer && <SongTitle/>}
          {!isMobile && showStemPlayer && playerChosen === tokenId && <SporePlaylist
                                                                        tokenId={tokenId}
                                                                        setShowStemPlayer={setShowStemPlayer}
                                                                        currentSong={currentSong} setCurrentSong={setCurrentSong}/>}
          {showStemPlayer && playerChosen === tokenId && <Credits/>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SporeOption);
