import React, { Suspense, Component } from 'react';
import ConnectWallet from 'components/connect-wallet/ConnectWallet.js';
import Zora from 'components/zora/Zora.js';
import NFTJacker from 'components/nft-jacker/NFTJacker.js';
import { BrowserRouter, StaticRouter, withRouter, Route, Switch } from 'react-router-dom';
import SporeLanding from "components/spore/SporeLanding.js";
import Demo from "components/demo/Demo.js";
import SporeWrapper from "components/spore/SporeWrapper.js";
import './App.scss';
import SporeLore from 'components/spore/SporeLore.js';
import '@rainbow-me/rainbowkit/dist/index.css';
import {
  getDefaultWallets,
  RainbowKitProvider,
    darkTheme
} from '@rainbow-me/rainbowkit';
import {
  chain,
  configureChains,
  createClient,
  WagmiConfig,
} from 'wagmi';
import {infuraProvider} from 'wagmi/providers/infura';
import { publicProvider } from 'wagmi/providers/public';

const infuraId = "2f3dce2af1044e88b4851c3d73c3d00c";
      
const { chains, provider } = configureChains(
    [chain.mainnet],
  [
      infuraProvider({ infuraId }),
      publicProvider()
  ]
);

const { connectors } = getDefaultWallets({
  appName: 'Spore Players',
  chains
});

const wagmiClient = createClient({
  autoConnect: true,
  connectors,
  provider
});



const App = (props) => {
    window.spores = true;
    return (
        <Suspense fallback={() => <div>Loading...</div>}>
          <WagmiConfig client={wagmiClient}>
          <RainbowKitProvider chains={chains}
                              theme={darkTheme({
                                  accentColor: 'black',
                                  accentColorForeground: 'white',
                                  borderRadius: 'small',
                              })}
          >
          <BrowserRouter>
            <Switch>
              <Route  path="/nftjacker/" 
                      render={(props) => <NFTJacker {...props} store={props.store}/>}/>
              <Route  path="/zora/" 
                      render={(props) => <Zora {...props} store={props.store}/>}/>
              <Route  path="/learn/" 
                      render={(props) => <SporeLanding learning={true} {...props} store={props.store}/>}/>
              <Route  path="/buy/" 
                      render={(props) => <SporeLanding buying={true} {...props} store={props.store}/>}/>
              <Route  path="/connect/" 
                      render={(props) => <ConnectWallet name="jeremih" {...props} store={props.store}/>}/>
              <Route  path="/jeremih/" 
                      render={(props) => <Demo name="jeremih" {...props} store={props.store}/>}/>
              <Route  path="/doodles1/" 
                      render={(props) => <Demo name="doodles1" {...props} store={props.store}/>}/>
              <Route  path="/doodles2/" 
                      render={(props) => <Demo name="doodles2" {...props} store={props.store}/>}/>
              <Route  path="/ragnarok/" 
                      render={(props) => <Demo name="ragnarok" {...props} store={props.store}/>}/>
              <Route  path="/lore" 
                      render={(props) => <SporeLore {...props} store={props.store}/>}/>
              <Route  path="/sp/:tokenId" 
                      render={(props) => <SporeLanding {...props} store={props.store}/>}/>
              <Route  path="/" 
                      render={(props) => <SporeLanding {...props} store={props.store}/>}/>
            </Switch>
          </BrowserRouter>
        </RainbowKitProvider>
        </WagmiConfig>
        </Suspense>
    );
};

export default App;

