import SporeContract from "decentralized/contracts/SporeContract.js";
import axios from 'axios';
import AudioUnit from 'engine/audio.js';
import Tone from 'tone';
import BackendQuery from "decentralized/BackendQuery.js";
import {NewSporeCreated} from "decentralized/EthereumEvents.js"
import ipfs from 'decentralized/Ipfs.js';
const bufferToArrayBuffer = require('buffer-to-arraybuffer');

export const getAllSpores = async (tokenId) => {

   let query = new BackendQuery(new SporeContract().contractAddress)
        .from(NewSporeCreated);
    let results = await query.executeQuery();
    for (let result of results) {
        if (result.sporeId === 4) {
            result.tokenId = 3;
        }
    }
    if (tokenId !== undefined) {
        results = results.filter(x => (x.tokenId === null && tokenId === 1) || x.tokenId === tokenId);
    }
    return results;
};

export const getLatestSpore = async (spore, tokenId, setSporeMetadata) => {
    let query = new BackendQuery(new SporeContract().contractAddress)
        .from(NewSporeCreated);

    if (spore) {
        query.where("sporeId", spore.sporeId);
    }

    let results = await query.executeQuery();
    console.log("RESULTS=", results, spore, query);
    for (let result of results) {
        if (result.sporeId === 4) {
            result.tokenId = 3;
        }
    }
    if (tokenId !== undefined) {
        results = results.filter(x => (x.tokenId === null && tokenId === 1) || x.tokenId === tokenId);
    }
    if (results.length === 0) {
        return null;
    }
    let latest = results[0];

    setSporeMetadata(latest);

    let sporeHash = latest.sporeHash;
    let dataHash = latest.dataHash;


    let dateA = new Date().getTime();
    let blob = await ipfs.getSound(sporeHash, 10000000);
    let dateB = new Date().getTime();
    let audioBuffer = await Tone.Master.context.decodeAudioData(bufferToArrayBuffer(blob));
    let player = new AudioUnit(Tone.Master.context);
    player.setBuffer(audioBuffer);
    let ret = {
        audioUnit: player, 
        audioBuffer,
        ... latest
    };

    if (dataHash) {
        console.log("data hash=", dataHash);
        ret.heat = await ipfs.getJson(dataHash);
    }
    return ret; 
};

export const pinAll = async () => {
    let query = new BackendQuery(new SporeContract().contractAddress)
        .from(NewSporeCreated);

    let results = await query.executeQuery();
    for (let result of results) {
        await saveOnPinata(result.sporeHash);
    }

};

const saveOnPinata = (hash) => {
    return new Promise((resolve) => {
    const url = `https://api.pinata.cloud/pinning/pinByHash`;
    axios
        .post(url, 
            {
                hashToPin: hash,
            },
              {
            headers: {
                pinata_api_key: '819763bfd08f709bc8f1',
                pinata_secret_api_key: '48cd285bcb2a634fa7d1ae033779793c655baf3faa1fc68bf235b1f15f8549a1'
            }
        })
        .then(function (response) {
            resolve();
            //handle your response here
        })
        .catch(function (error) {
            //handle error here
        });
    });
};
