import UnknownProgressBar from 'components/progressbar/UnknownProgressBar.js';
import ipfs from 'decentralized/Ipfs.js';
import {isMobile} from 'react-device-detect';
import React, { useRef, useEffect, useCallback, useState} from 'react';
import PlayerHelper from './PlayerHelper.js';
import Credits from './Credits.js';
import Sidebar from 'components/sidebar/Sidebar.js';
import {useConsumeWheel} from "components/hooks/useConsumeWheel.js";
import SporesView from './SporesView.js';
import {pinAll} from './getSpores.js';
import {sleep} from 'utils/sleep.js';
import {useShader} from './sketch.js';
import StemPlayer from './StemPlayer.js';
import SimplePlayer from './SimplePlayer.js';
import {connect} from 'react-redux';
import './spore.scss';

const Spore = ({setPlayerChosen, playerChosen ,setShowStemPlayer, showStemPlayer, ...props}) => { 
    let [preset, setPreset] = useState();
    let [uploading, setUploading] = useState();

    let tokenId = props.match && props.match.params.tokenId;
    if (tokenId) {
        tokenId = parseInt(tokenId);
    }
    let audioUnit = useRef();
    let [showCredits, setShowCredits] = useState(undefined);
    //let [playerChosen, setPlayerChosen] = useState(tokenId);
    //let [showStemPlayer, setShowStemPlayer] = useState(tokenId !== undefined);
    let [showSpores, setShowSpores] = useState(false);
    let [s, setS] = useState(false);
    let [sequencerEngine, setSequencerEngine] = useState();
    let [canvasBackground, setCanvasBackground]= useState();
    let [customVideo, setCustomVideo] = useState();
    let ref = useRef();

 //   useEffect(() => {
//        pinAll();
  //  }, []);

    useEffect(() => {
        setShowSpores(false);
    }, [showStemPlayer, setShowSpores]);

    useEffect(() => {
        window.addEventListener("mousewheel", preventDefault, {passive: false});
        return () => window.removeEventListener("mousewheel", preventDefault);
    }, []);

    useEffect(() => {
        if (audioUnit.current && audioUnit.current.buffer) {
            audioUnit.current.dispose();
        }
    }, [showStemPlayer]);

    const preventDefault = (e) => e.preventDefault();

    useEffect(() => {
        document. title = "Spores"
    }, []);

    //useShader(ref, showStemPlayer);

    let className = "spore";
    if (showStemPlayer) {
        className += " showing-spore-player";
    }
    if (showSpores) {
        className += " showing-spores";
    }

    useEffect(() => {

        let url = window.localStorage.getItem("custom-video-background");
        if (url) {
            setCustomVideo(url);
        }
    }, [setCustomVideo]);

    useEffect(() => {
        window.addEventListener("drop", onDrop);

        window.addEventListener("dragenter", function(e) {
            e.preventDefault();
            e.dataTransfer.effectAllowed = "none";
            e.dataTransfer.dropEffect = "none";
        }, false);
        
        window.addEventListener("dragover", function(e) {
            e.preventDefault();
            e.dataTransfer.effectAllowed = "none";
            //e.dataTransfer.dropEffect = "none";
            return false;
        });
    }, [setCustomVideo, setUploading]);

    const onDrop = useCallback((ev) => {
        ev.preventDefault();
        ev.dataTransfer.effectAllowed = "none";
        ev.dataTransfer.dropEffect = "none";
        
        if (ev.dataTransfer.items) {
            // Use DataTransferItemList interface to access the file(s)
            let len = ev.dataTransfer.items.length;
            for (var i = 0; i < ev.dataTransfer.items.length; i++) {
                // If dropped items aren't files, reject them
                if (ev.dataTransfer.items[i].kind === 'file') {
                    setUploading(true);
                    uploadFile(ev.dataTransfer.items[i].getAsFile())
                        .then(url => {
                            if (url) {
                                setCustomVideo(url);
                                window.localStorage.setItem("custom-video-background", url);
                            } 
                            setUploading(false);
                        });
                }
            }
        }
    }, [setCustomVideo, setUploading]);

    useEffect(() => {
        console.log("yop");
        if (showStemPlayer) {
            let last = window.localStorage.getItem("stem player loaded at");
            console.log("last = ", last);
            if (!last || new Date().getTime - last < 10*60) {
                // if they haven't seen the tutorial in 10 minutes show it to remind them
                setShowCredits(true);
                window.localStorage.setItem("stem player loaded at", new Date().getTime());
            }
        }
    }, [showStemPlayer, setShowCredits]);
          
    if (customVideo) {
        className += " custom-video-background";
    }
    return (
        <div
          onDrop={onDrop}
          ref={ref} className={className}>
          {uploading && <div className="video-progress-bar"><UnknownProgressBar width={200} text="Uploading Video Background"/></div>}
          <video
            playsInline
            className="rain-video"
            autoPlay
            loop
            muted
            src={customVideo ? customVideo : "/mushrooms-medium.mp4"}/>
          {showCredits && (showStemPlayer ? <PlayerHelper clear={() => setShowCredits(undefined)}/> : <Credits setShowCredits={setShowCredits}/>)}
          {<Sidebar showStemPlayer={showStemPlayer || showSpores}
                     setShowStemPlayer={(x) => {
                         setShowStemPlayer(x);
                         setShowSpores(x);
                     }}
                     playerChosen={playerChosen} setPlayerChosen={setPlayerChosen}/>}
          {(playerChosen && !showStemPlayer && showSpores) && <SporesView
                                                                setPreset={setPreset}
                                                                preset={preset}
                                                                store={props.store}
                                                                audioUnit={audioUnit}
                                                                tokenId={playerChosen} setShowStemPlayer={setShowStemPlayer}/>}
          {playerChosen && !showSpores && (showStemPlayer ? <StemPlayer
                                                              setShowCredits={setShowCredits}
                                                              showCredits={showCredits}
                                                              setShowStemPlayer={setShowStemPlayer} tokenId={playerChosen}/> :
                                           <SimplePlayer
                                             setPreset={setPreset}
                                             preset={preset}
                                             store={props.store}
                                             audioUnit={audioUnit}
                                             tokenId={playerChosen} setShowStemPlayer={setShowStemPlayer}/>)}
          {(!isMobile || showSpores) && playerChosen && (showSpores || showStemPlayer) &&
           <div
             onClick={() => {
                 setShowStemPlayer(false);
                 setShowSpores(false);
             }}
             className="spore-button pushable stem-player-button">
             See Latest Spore
             <img className="spore-icon one-spore" src="/one-spore.svg"/>
           </div>}
          {playerChosen && !showSpores && !showStemPlayer &&
           <div
             onClick={() => setShowSpores(true)}
             className="spore-button pushable stem-player-button">
             See All Spores <img className="spore-icon" src="/spores-icon-glow2.svg"/>
           </div>}
          <div onClick={() => setShowCredits(true)} className="explainer-button">
            ?
          </div>
        </div>
    );
};

const uploadFile = (file, setter) => {
    return new Promise(resolve => {
        let reader = new FileReader();
        if (!file.name.includes('.mp4')) {
            window.alert("Only accepts .mp4");
            resolve();
            return;
        }
        reader.readAsArrayBuffer(file);
        reader.onloadend = async () => {
            let raw = reader.result;
            if (raw.byteLength > 3*17567767) {
                window.alert("Video file must be under 15 mb");
                resolve();
                return;
            }
            let hash = await ipfs.addArrayBuffer(raw);
            let url = "https://zequencer.io/ipfs/" + hash;
            resolve(url);
        };
    });
};
          
const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Spore);
